<template>
    <div>
        <van-swipe :autoplay="3000">
            <van-swipe-item v-for="(image, index) in bannerList" :key="index">
                <img v-lazy="image.image" />
                <!-- <img :src="image.image" :onerror="errorImg" /> -->
            </van-swipe-item>
        </van-swipe>
    </div>
</template>

<script>
import * as api from "api";
export default {
  name: "Home",
  data() {
    return {
      bannerList: [],
      //errorImg: 'this.src="' + require("../assets/images/default.jpg") + '"'
    };
  },
  mounted() {
    this.getNews();
  },
  methods: {
    getNews() {
      api.getNews().then(res => {
        let data = res.data;
        this.bannerList = data.top_stories;
      });
    }
  }
};
</script>

<style scoped>
img {
  width: 100%;
  height: auto;
}
</style>


